<template>
  <div class="login-wrap">
    <div class="nav_container">
      <div class="nav-centre">
        <img src="http://res.yujingceping.com/res/resources/front/imgs/Logo.png">
      </div>
    </div>
    <!--<div class="ms-title">教务管理系统</div>-->
    <div class="forgetBody">
      <div class="stepsWrap">
        <el-steps :space="230" :active="active" center>
          <el-step title="验证身份" icon="document"></el-step>
          <el-step title="重置密码" icon="edit"></el-step>
          <el-step title="找回成功" icon="check"></el-step>
        </el-steps>
      </div>
      <div class="pageOne" v-show="active == 1">
        <div class="ms-login">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="60px" class="demo-ruleForm">
            <el-form-item prop="phone" label="手机号:">
              <el-input :maxlength="11" @change="queryMsgAllow(ruleForm)" id="phone" v-model="ruleForm.phone" placeholder="手机号码"></el-input>
            </el-form-item>
            <el-form-item prop="captcha" label="验证码:">
              <el-input :maxlength="6" id="captcha" @change="queryMsgAllow(ruleForm)" v-model="ruleForm.captcha" placeholder="验证码" @keyup.enter.native="submitForm('ruleForm')"></el-input>
              <div class="captchaWrap">
                <div @click="getCaptcha('ruleForm')" class="timeButton">
                  <input id="countD"  :class="captchaClick ? 'allowCountDown' : 'notAllowClick' " readonly type="text" v-model="countDown">
                </div>
                <!--<img @click="reloadImg" :src="captchaImg" alt="验证码">-->
              </div>

            </el-form-item>
            <div class="login-btn">
              <el-button :class="btnStatus ? 'primary' : 'notAllowBtn' " type="primary"  @click="submitForm('ruleForm')">登录</el-button>
            </div>
            <p style="font-size:12px;line-height:30px;color:#999;text-align: left">Tips : 验证码有效期5分钟。</p>
            <!--<router-link to="/forgetPassword"><span class="forgetPassword">忘记密码</span></router-link>-->
          </el-form>
        </div>
      </div>
      <div class="pageOne" v-show="active == 2">
        <div class="ms-login">
          <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="68px" class="demo-ruleForm">
            <el-form-item label="输入密码" prop="pass">
              <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="重新输入" prop="checkPass">
              <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
            </el-form-item>

            <div class="login-btn">
              <el-button type="primary" class="primary" @click="submitFormTwo('ruleForm2')">确定</el-button>
            </div>
          </el-form>
        </div>
      </div>
      <div class="pageOne" v-show="active == 3">
        <div class="ms-login">
          <div class="tipRight">
            <i class="el-icon-circle-check doneRight"></i>
            <p class="tipText">密码修改成功！</p>
          </div>
          <div class="login-btn">
            <el-button type="primary" class="primary" @click="submitFormThree()">确定</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script scoped>
  export default {
    data: function(){
      var validatePhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入手机号码'));
        } else {
          var patrn=/^1[34578]\d{9}$/;
          if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
            callback(new Error('请输入正确的11位手机号码'));
          }else{
            callback();
          }
        }
      };
      var validateCaptcha = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入验证码'));
        } else {
          var patrn=/^[A-Za-z0-9]+$/;
          if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
            callback(new Error('数字字母的组合'));
          }else{
            callback();
          }
        }
      };
      // 第二步骤验证
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          var patrn=/^(\w){6,19}$/;
          if(!patrn.exec(value)){
            callback(new Error('至少6位不包含空格及特殊符号的密码'));
          }else {
            if (this.ruleForm2.checkPass !== '') {
              this.$refs.ruleForm2.validateField('checkPass');
            }
            callback();
          }
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          phone: '',
          captcha: ''
        },
        rules: {
          phone: [
            { validator:validatePhone, trigger: 'blur' }
          ],
          captcha: [
            { validator:validateCaptcha, trigger: 'blur' }
          ]
        },
        ruleForm2: {
          pass: '',
          checkPass: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        },
        captchaImg:"http://60.205.115.173:8888/yitai-educational/captcha?ts="+new Date().getTime(),
        btnStatus : false,
        countDown : "获取验证码",
        captchaClick: true,
        active : 1
      }
    },
    methods: {
      submitForm(formName) {
        const self = this;
        self.$refs[formName].validate((valid) => {
          if (valid) {
            self.$ajax('password/find/sms/verify',
              self.ruleForm
              ).then(function (response) {
                  self.active = 2;
                  console.log(response);
                }).catch(function (error) {
                    console.log(error);
                });

          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      reloadImg(){
        this.captchaImg = "http://60.205.115.173:8888/yitai-educational/captcha?ts="+new Date().getTime()
      },
      queryMsgAllow(val){
        if (val.phone.length>0 && val.captcha.length>0){
          this.btnStatus = true;
        }else {
          this.btnStatus = false;
        }
      },
      getCaptcha(formName){console.log(this.aaa);
        if (this.captchaClick == false){
          console.log(1);
          return;
        }else {
          const self = this;
          var phone = "phone";
          self.$refs[formName].validateField(phone,(error) => {
            if (error == ''){
              self.captchaClick = false;
              self.countDown = "60秒后可重发";
              var count = 59;
              var timer = setInterval(function () {
                self.countDown = count+"秒后可重发";
                count -= 1;
                if (count<1){
                  self.countDown = "获取验证码";
                  self.captchaClick = true;
                  clearInterval(timer);
                }
              },1000);
              self.$ajax('password/find/sms/send',
                {"phone":self.ruleForm.phone}
              ).then(function (response) {

//                    if (response.code == 200){
//
//                    }else {
//                      self.$alert(response.message, '提示', {
//                        confirmButtonText: '确定',
//                        callback: action => {
////                  this.$message({
////                    type: 'info',
////                    message: `action: ${ action }`
////                  });
//                        }
//                      });
//                    }

              }).catch(function (error) {
                console.log(error);
              });
            }
          });
        }
      },
      submitFormTwo(formName) {
          var $this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
//              console.log(self,$this)
//            alert('submit!');
            $this.$ajax('password/find/submit',
              {
                  "phone": $this.ruleForm.phone,
                  "password" : $this.ruleForm2.checkPass
              }
            ).then(function (response) {
                $this.active = 3;
//                    if (response.code == 200){
//
//                    }else {
//                      self.$alert(response.message, '提示', {
//                        confirmButtonText: '确定',
//                        callback: action => {
////                  this.$message({
////                    type: 'info',
////                    message: `action: ${ action }`
////                  });
//                        }
//                      });
//                    }

            }).catch(function (error) {
              console.log(error);
            });

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      submitFormThree(){
        this.$router.push('/login');
      }
    }
  }
</script>

<style>
  .login-wrap{
    margin: 0 auto;
  }
  .nav_container {
    width: 100%;
    height: auto;
    position: relative;
    z-index: 100;
    background: #fff;
  }
  .nav-centre {
    width: 1024px;
    height: 80px;
    background: #fff;
    margin: 0 auto;
    overflow: hidden;
  }
  .nav_container img {
    width: 150px;
    height: 38px;
    margin-top: 21px;
    float: left;
    margin-right: 57px;
  }
  .forgetBody {
    padding-top: 80px;
    width: 1024px;
    background: #ffffff;
    border-top: 5px solid #0C9EE9;
    margin:  0 auto;
    text-align: center;
  }
  .pageOne {
    margin: 0 auto;
  }
  .ms-login{
    display: inline-block;
    width:280px;
    height:230px;
    padding:40px 30px 40px 14px;
    border-radius: 5px;
    background: #fff;
  }
  .login-btn{
    margin-top: 40px;
    text-align: center;
  }
  .loginBtn button{
    width: 100%;
  }
  .login-btn button{
    width:100%;
    height:36px;
  }
  .el-form-item__content {
    text-align: left;
  }
  #captcha {
    width: 100px;
    margin-left: 0px;
  }
  #captcha input{
    display: inline-block;
    width: 100px;
  }
  .captchaWrap {
    vertical-align: top;
    margin-left: 15px;
    display: inline-block;
    width: 90px;
    height: 36px;
  }
  .captchaWrap img {
    cursor: pointer;
    display: inline-block;
    width: 100%;
    height: 100%;
  }
  .el-form-item__content .captchaText{
    position: absolute;
    height: 0;
    right: 0;
    top: 35px;
    font-size: 12px;
    cursor: pointer;
    color: mediumblue;
    text-align: right;
    display: inline-block;
    text-decoration:underline;
    -moz-user-select:none;
    -webkit-user-select:none;
    user-select:none;
  }
  .captchaText:active {
    color: blue;
    -moz-user-select:none;
    -webkit-user-select:none;
    user-select:none;
  }
  .login-btn .notAllowBtn {
    background: gray;
    width:100%;
    height:36px;
    border: 1px solid gray;
  }
  .notAllowBtn:hover {
    background: gray;
    width:100%;
    height:36px;
  }
  .login-btn .notAllowBtn:active {
    background: gray;
    width:100%;
    height:36px;
  }
  .forgetPassword {
    position: absolute;
    right: 30px;
    bottom: 18px;
    font-size: 12px;
    cursor: pointer;
    color: grey;
    text-align: right;
    display: inline-block;
  }
  .forgetPassword:hover {
    color: mediumblue;
    text-decoration:underline;
  }
  .timeButton {
    box-sizing: border-box;
    width: 100px;
    height: 36px;
    font-size: 12px;
    text-align: center;
    line-height: 36px;
    /*background: #0C9EE9;*/
    color: #fff;
    border: none;
    border-radius: 5px;
    position: absolute;
    margin-left: 2px;
    cursor: pointer;
    -moz-user-select:none;
    -webkit-user-select:none;
    user-select:none;
  }
  .notAllowClick {
    background: grey;
    box-sizing: border-box;
    width: 100px;
    height: 36px;
    border-radius: 5px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
    border: none;
  }
  .allowCountDown{
    box-sizing: border-box;
    width: 100px;
    height: 36px;
    border-radius: 5px;
    background: #0C9EE9;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
    border: none;
  }
  /*.timeButton:hover,.timeButton:active{*/
    /*background:deepskyblue;*/
  /*}*/
  .tipRight{
    padding: 30px;
    overflow: hidden;
  }
  .tipRight .doneRight{
    font-size: 40px;
    color: green;
    float: left;
  }
  .tipRight .tipText{
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    font-weight: 600;
    vertical-align: middle;
  }

</style>



